body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Nunito Sans, sans-serif;
  position: relative;
  overflow-x: hidden;
}

html {
  overflow-x: hidden;
}

:root {
  --btn-bg-color: #283618;
  --grey-bg: #f1f0f0;
  --cream: #fefae0;
  --details: #bc6c25;
  --list: #eae7e7;
  --selected: #e5e0e0;
}

.navbar {
  height: 7vh;
  box-sizing: border-box;
  background-image: linear-gradient(#f2cda3, #edc291, #e8b780, #e3ac6f, #dda15e);
  padding: .7em;
  display: flex;
}

.logo-brandname {
  flex-basis: 50%;
  align-items: center;
  padding-left: 1em;
  transition: all .3s ease-in-out;
  display: flex;
}

.logo {
  max-width: 2rem;
  margin-right: .5rem;
  transition: all .3s ease-in-out;
}

.brand-name {
  margin: 0;
  font-family: Marck Script, cursive;
  font-size: 1.8rem;
  font-weight: 550;
}

.navbar-icon {
  flex-basis: 50%;
  justify-content: flex-end;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

.navbar-icon img {
  cursor: pointer;
  max-width: 1.5rem;
  margin: 0 .5rem;
}

.navbar-icon img:active {
  transition: all .3s ease-in-out;
  transform: translateY(-3px);
}

.navigation-searchbar-container {
  width: 50%;
  margin-left: 5rem;
  transition: all .3s ease-in-out;
  display: none;
  position: relative;
}

.searchbar-input {
  box-sizing: border-box;
  width: 100%;
  border: none;
  border-radius: 3em;
  outline: none;
  padding: .6em;
  font-size: 1.1rem;
  transition: all .3s ease-in-out;
}

.searchbar-btn {
  height: 100%;
  box-sizing: border-box;
  background-color: var(--btn-bg-color);
  border: none;
  border-radius: 3em;
  outline: none;
  justify-content: center;
  align-items: center;
  padding: 1em 2em;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.searchbar-btn img {
  width: 1.3rem;
  display: inline-block;
}

.navigation-menu-container {
  flex-basis: 50%;
  justify-content: space-evenly;
  align-items: center;
  display: none;
}

.navigation-menu-container p {
  align-items: center;
  display: flex;
}

.navigation-menu-icon {
  width: 1.5rem;
  margin-right: .5rem;
}

#bookmark-hover-container {
  background-color: var(--grey-bg);
  z-index: 10;
  width: 60%;
  max-height: 20rem;
  transition: all .4s ease-in-out;
  display: none;
  position: absolute;
  top: 5rem;
  left: 35%;
  overflow-y: scroll;
  box-shadow: 0 3px 13px -1px #000000bf;
}

#bookmark-hover-container::-webkit-scrollbar {
  width: 5px;
  background-color: #fff;
}

#bookmark-hover-container::-webkit-scrollbar-thumb {
  width: 3px;
  background-color: #c5c4c4;
  border-radius: 1em;
}

@media (min-width: 51.25em) {
  .navbar {
    height: 10vh;
  }

  .recipe-mobile-scroll-container {
    grid-auto-columns: 22%;
  }

  .navbar-icon img {
    cursor: pointer;
    max-width: 1.7rem;
    margin: 0 .5rem;
  }

  .brand-name {
    font-size: 2rem;
  }

  .logo {
    max-width: 2.5rem;
  }
}

@media (min-width: 85.375em) {
  .navbar {
    padding: 0;
  }

  .navbar-icon {
    display: none;
  }

  .navigation-menu-container {
    display: flex;
    position: relative;
  }

  .navigation-searchbar-container {
    display: block;
  }

  .navigation-menu-list {
    box-sizing: border-box;
    height: 100%;
    padding: 1em;
    font-size: 1.1rem;
    transition: all .3s ease-in-out;
  }

  .navigation-menu-container .navigation-menu-list:hover {
    background-color: #d49c5c;
  }

  .bookmark-hover:hover ~ #bookmark-hover-container, #bookmark-hover-container:hover {
    transition: all .4s ease-in-out;
    display: block;
  }
}

.recipe-spinner-container {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.recipe-spinner {
  width: 5rem;
}

.recipe-mobile-scroll-container {
  min-height: 18vh;
  box-sizing: border-box;
  scroll-behavior: smooth;
  grid-auto-columns: 35%;
  grid-auto-flow: column;
  display: grid;
  overflow-x: scroll;
  box-shadow: 0 10px 14px -7px #000000bf;
}

.recipe-mobile-scroll-container::-webkit-scrollbar {
  height: 5px;
  background-color: #fff;
}

.recipe-mobile-scroll-container::-webkit-scrollbar-thumb {
  height: 3px;
  background-color: #c5c4c4;
  border-radius: 1em;
}

.recipe-container {
  max-height: 100%;
  text-align: center;
  background-color: #fffcfc;
  border-right: .2px solid #e7e4e4;
  flex-direction: column;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
}

.recipe-id {
  z-index: 29;
  background-color: #d49c5c;
}

.recipe-container-active, .recipe-container:hover {
  background-color: var(--selected);
}

.recipe-container a {
  width: 100%;
  height: 100%;
  color: #000;
  text-decoration: none;
}

.recipe-mobile-name {
  margin-bottom: 0;
  font-size: .8rem;
  font-weight: 700;
}

.recipe-mobile-site {
  color: var(--details);
  margin-top: .3rem;
  margin-bottom: 1rem;
  font-size: .65rem;
}

.recipe-mobile-image {
  width: 4.5rem;
  height: 4.5rem;
  border-radius: 50%;
}

.fa-chevron-right, .fa-chevron-left {
  color: #9f9c99;
  font-size: 1.5rem;
  position: absolute;
  top: 10%;
}

.fa-chevron-right, .fa-chevron-left:hover {
  cursor: pointer;
}

.fa-chevron-right {
  right: .3rem;
}

.recipe-banner-container {
  height: 12rem;
  background-color: #dda15e;
  margin-bottom: 1rem;
  position: relative;
}

.recipe-banner-image {
  width: 100%;
  object-fit: cover;
  object-position: center;
  height: 100%;
  opacity: .7;
}

.recipe-name-container {
  box-sizing: border-box;
  text-transform: uppercase;
  color: var(--cream);
  background-color: #283618a0;
  padding: 1em 2em;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
}

.recipe-result-message {
  text-align: center;
  height: 100%;
  width: 100%;
}

@media (min-width: 30em) {
  .recipe-mobile-scroll-container {
    height: 11rem;
  }

  .recipe-mobile-name {
    font-size: 1rem;
  }

  .recipe-mobile-site {
    font-size: .75rem;
  }

  .recipe-mobile-image {
    width: 5rem;
  }

  .recipe-name-container {
    font-size: 1.2rem;
  }
}

@media (min-width: 51.25em) {
  .recipe-mobile-scroll-container {
    grid-auto-columns: 22%;
  }

  .recipe-banner-container {
    height: 15rem;
  }

  .navbar-icon {
    padding-right: 2em;
  }

  .logo-brandname {
    padding-left: 2em;
  }

  .fa-chevron-right, .fa-chevron-left {
    top: 9%;
  }
}

@media (min-width: 85.375em) {
  .recipe-banner-container {
    height: 17rem;
  }

  .recipe-mobile-scroll-container {
    cursor: grab;
    grid-auto-columns: 13%;
  }

  .recipe-container:hover {
    background-color: var(--selected);
    transform: translateY(-2px);
  }

  .fa-chevron-right, .fa-chevron-left {
    z-index: 10;
  }
}

.no-search-container, .message, .no-click-container, .spinner-container {
  height: 75vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.no-click-text {
  height: 75vh;
}

.no-search-text, .no-click-text {
  width: 100%;
  text-align: center;
  font-size: 1.5rem;
}

.spinner {
  width: 5rem;
}

.fa-check {
  color: var(--details);
  margin-right: .5rem;
}

.recipe-ingredients-title {
  text-align: center;
  margin-bottom: .5rem;
  font-weight: 650;
}

.recipe-ingredients-container {
  flex-direction: column;
  grid-area: a;
  align-items: center;
  display: flex;
}

.recipe-ingredients-container-list ul {
  box-sizing: border-box;
  column-width: 50%;
  column-count: 2;
  padding: .5em .5em .5em 1.5em;
}

.recipe-ingredients-list {
  font-size: .95rem;
  line-height: 2rem;
  list-style: none;
}

.add-shopping-list-container {
  width: 100%;
  grid-area: c;
  justify-content: center;
  display: flex;
}

.add-shopping-list-button {
  color: var(--cream);
  box-sizing: border-box;
  background-image: linear-gradient(to bottom right, #629626, #537d24, #446420, #364c1d, #283618);
  border: none;
  border-radius: 5em;
  align-items: center;
  margin-top: .5rem;
  margin-left: 10rem;
  padding: .8em 1.5em;
  font-family: Nunito Sans, sans-serif;
  display: flex;
}

.add-shopping-list-button:hover {
  cursor: pointer;
}

.btn-cart-icon {
  color: var(--cream);
  width: 1rem;
  margin-left: .5rem;
}

.recipe-cooking-minutes-container {
  box-sizing: border-box;
  width: 100%;
  height: 10rem;
  grid-area: b;
  justify-content: space-around;
  align-items: center;
  padding: 1em;
  font-size: 1rem;
  display: flex;
}

.recipe-cooking-minutes-container p {
  align-items: center;
  display: flex;
}

.serving-time-icon, .serving-people-icon {
  width: 1.5rem;
  margin-right: .3rem;
}

.serving-button {
  border: .8px solid var(--details);
  color: var(--details);
  background-color: #0000;
  border-radius: 1em;
  outline: none;
  margin: .2rem;
}

.serving-time, .serving-people {
  margin-right: .3rem;
}

.btn-add-bookmark {
  box-sizing: border-box;
  background-image: linear-gradient(to bottom right, #629626, #537d24, #446420, #364c1d, #283618);
  border: none;
  border-radius: 50%;
  outline: none;
  padding: 1em;
}

.btn-add-bookmark img {
  width: 1.5rem;
}

@media (min-width: 30em) {
  .recipe-ingredients-title, .recipe-cooking-minutes-container {
    font-size: 1.2rem;
  }

  .recipe-ingredients-list {
    font-size: 1rem;
  }

  .add-shopping-list-button {
    margin-left: 11rem;
    font-size: 1.1rem;
  }

  .btn-add-bookmark {
    padding: 1em;
  }

  .btn-add-bookmark img {
    width: 1.7rem;
  }
}

@media (min-width: 51.25em) {
  .recipe-ingredients-title, .recipe-cooking-minutes-container {
    font-size: 1.5rem;
  }

  .recipe-ingredients-list {
    font-size: 1.2rem;
  }

  .recipe-ingredients-container-list {
    width: 80%;
    justify-content: center;
    display: flex;
  }

  .recipe-ingredients-container-list ul {
    box-sizing: border-box;
    column-width: 50%;
    column-count: 2;
    column-gap: 5rem;
    padding: .5em .5em .5em 2em;
  }

  .recipe-ingredients-container-list ul li {
    line-height: 3rem;
  }

  .add-shopping-list-container {
    width: 65%;
    grid-area: c;
    justify-content: flex-end;
    margin: auto;
    display: flex;
  }

  .add-shopping-list-button {
    font-size: 1.2rem;
  }

  .btn-cart-icon {
    width: 1.5rem;
  }

  .recipe-cooking-minutes-container {
    width: 80%;
    margin: auto;
  }

  .serving-time-icon, .serving-people-icon {
    width: 2rem;
  }

  .btn-add-bookmark {
    padding: 1em;
  }

  .btn-add-bookmark img {
    width: 2rem;
  }
}

@media (min-width: 85.375em) {
  .no-search-container {
    height: 90vh;
  }

  .no-search-text {
    font-size: 2rem;
  }

  .no-click-container {
    height: 68vh;
  }

  .no-click-text {
    font-size: 2rem;
  }

  .main-content {
    grid-template-areas: "a a b"
                         "a a b"
                         "c c c";
    display: grid;
  }

  .recipe-cooking-minutes-container {
    background-color: var(--grey-bg);
    box-sizing: border-box;
    width: 50%;
    height: 25rem;
    border-radius: 1em;
    flex-direction: column;
    grid-area: b;
    justify-content: space-around;
    align-items: center;
    margin-right: 15rem;
    padding: 1em;
    display: flex;
  }

  .recipe-ingredients-container {
    flex-direction: column;
    grid-area: a;
    align-items: center;
    margin-top: 3rem;
    margin-left: 5rem;
    display: flex;
  }

  .add-shopping-list-container {
    width: 100%;
    grid-area: c;
    justify-content: center;
    margin-top: 2rem;
    margin-bottom: 3rem;
    display: flex;
  }

  .add-shopping-list-button {
    margin-top: 0;
    margin-left: 0;
    padding: .8em 1.5em;
  }
}

.how-to-cook-section {
  box-sizing: border-box;
  background-color: var(--grey-bg);
  height: 13rem;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em;
  display: flex;
}

.how-to-cook-title {
  margin: 0;
  font-size: 1.2rem;
  font-weight: 700;
}

.how-to-cook-btn {
  box-sizing: border-box;
  color: var(--cream);
  background-image: linear-gradient(to bottom right, #629626, #537d24, #446420, #364c1d, #283618);
  border: none;
  border-radius: 3em;
  outline: none;
  align-items: center;
  padding: .8em 3em;
  display: flex;
}

.how-to-cook-text {
  width: 95%;
  text-align: center;
}

.direction-right {
  width: 1.3rem;
  margin-left: 1rem;
}

@media (min-width: 51.25em) {
  .how-to-cook-section {
    height: 16rem;
  }

  .how-to-cook-title {
    font-size: 1.5rem;
  }

  .how-to-cook-text {
    width: 75%;
    text-align: center;
    font-size: 1.3rem;
  }

  .how-to-cook-btn {
    padding: .7em 3em;
    font-size: 1.2rem;
  }

  .direction-right {
    width: 2rem;
  }
}

@media (min-width: 85.375em) {
  .how-to-cook-text {
    width: 50%;
    text-align: center;
    font-size: 1.3rem;
  }
}

.search-container {
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
  background-color: #fff;
  padding: 1.5em;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 0;
  left: 0;
  overflow-x: hidden;
}

.search-sub-container {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.search-input-mobile-container {
  width: 85%;
  position: relative;
}

.search-input-mobile {
  width: 100%;
  box-sizing: border-box;
  background-color: var(--grey-bg);
  border: none;
  border-radius: 2em;
  padding: .6em;
  font-size: 1.3rem;
}

.search-input-mobile:focus {
  outline: none;
}

.bi-search {
  border: none;
  outline: none;
  font-size: 1.5rem;
  transition: all .3s ease-in-out;
  position: absolute;
  top: .5rem;
  right: 1rem;
}

.bi-search:active {
  transform: translateY(-2px);
}

.tooltip-mobile {
  background-color: var(--selected);
  opacity: 1;
  border-radius: 1em;
  margin: 0;
  padding: .1em .8em;
  transition: all 1s ease-in-out;
  position: absolute;
  top: 4rem;
  left: 0;
}

.tooltip-mobile:before {
  content: "";
  border-left: 10px solid #0000;
  border-right: 10px solid #0000;
  border-bottom: 20px solid var(--selected);
  position: absolute;
  top: -.8rem;
  left: 1rem;
}

@media (min-width: 85.375em) {
  .search-container {
    display: none;
  }
}

.bookmark-mobile-container {
  background-color: var(--grey-bg);
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  opacity: 1;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: scroll;
}

.bookmark-list-container {
  background-image: linear-gradient(to bottom right, #f4f4f4, #f1f1f1, #efeeee, #eceaeb, #eae7e7);
  margin-top: 3rem;
}

.close-bookmark-mobile {
  width: 2rem;
  position: absolute;
  top: 10px;
  right: 10px;
}

.bookmark-icon {
  width: 2rem;
  margin-right: .5rem;
}

.bookmark-mobile-text {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  margin-top: 8rem;
  font-size: 1.5rem;
  display: flex;
}

.bookmark-list a {
  box-sizing: border-box;
  border-bottom: .2px solid #bab8b8;
  grid-template-areas: "recipe-image recipe-name recipe-name"
                       "recipe-image recipe-site recipe-site";
  padding: 2em;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: grid;
  position: relative;
}

.bookmark-list:active {
  background-color: var(--selected);
}

.delete-bookmark {
  width: 2rem;
  position: absolute;
  top: 0;
  right: 0;
}

.bookmark-food-sample {
  width: 4.5rem;
  height: 4.4rem;
  border-radius: 50%;
  grid-area: recipe-image;
  margin: auto;
  display: inline-block;
}

.bookmark-list .recipe-name {
  color: #000;
  grid-area: recipe-name;
  margin: 0;
  font-weight: 650;
}

.recipe-site {
  color: var(--details);
  grid-area: recipe-site;
  margin: 0;
}

.no-bookmark {
  background-color: var(--grey-bg);
  text-align: center;
  margin-top: 5rem;
  padding: 0 1em;
  font-size: 1.5rem;
}

@media (min-width: 51.25em) {
  .bookmark-icon {
    width: 2.5rem;
  }

  .bookmark-mobile-text {
    font-size: 2rem;
  }

  .bookmark-list {
    box-sizing: border-box;
    border-bottom: .2px solid #bab8b8;
    grid-template-areas: "recipe-image recipe-name recipe-name"
                         "recipe-image recipe-site recipe-site";
    padding: 2em 5em;
    display: grid;
    position: relative;
  }

  .bookmark-food-sample {
    width: 6rem;
    grid-area: recipe-image;
  }

  .recipe-name {
    grid-area: recipe-name;
    font-size: 1.5rem;
  }

  .recipe-site {
    grid-area: recipe-site;
    font-size: 1.2rem;
  }
}

@media (min-width: 85.375em) {
  .bookmark-list {
    box-sizing: border-box;
    border-bottom: .2px solid #bab8b8;
    grid-template-areas: "recipe-image recipe-name recipe-name"
                         "recipe-image recipe-site recipe-site";
    padding: 1em 2em;
    display: grid;
    position: relative;
  }

  .bookmark-list:hover {
    box-sizing: border-box;
    background-color: var(--selected);
    transform: translateY(-3px);
  }

  .delete-bookmark {
    visibility: hidden;
  }

  .bookmark-list:hover .delete-bookmark {
    visibility: visible;
  }

  .bookmark-food-sample {
    width: 4rem;
    grid-area: recipe-image;
  }

  .recipe-name {
    grid-area: recipe-name;
    font-size: 1rem;
  }

  .recipe-site {
    grid-area: recipe-site;
    font-size: .8rem;
  }
}

.hamburger-container {
  background-color: var(--grey-bg);
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  opacity: 1;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
}

.hamburger-sub-container {
  margin-top: 10rem;
}

.hamburger-choice {
  opacity: 1;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
  padding: 1em;
  font-size: 1.5rem;
  transition: all .5s ease-in-out;
  display: flex;
}

.hamburger-choice:active {
  background-color: var(--selected);
}

.hamburger-choice-icon {
  width: 2rem;
  margin-right: 1em;
}

.close-hamburger {
  width: 2rem;
  position: absolute;
  top: 10px;
  right: 10px;
}

@media (min-width: 51.25em) {
  .hamburger-choice-icon {
    width: 2.5rem;
  }

  .hamburger-choice {
    font-size: 2rem;
  }
}

@media (min-width: 85.375em) {
  .hamburger-container {
    display: none;
  }
}

.add-recipe-container {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  opacity: 1;
  z-index: 10;
  background-color: #fff;
  transition: all .3s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
  overflow-y: scroll;
